import React, { Component } from 'react';
import LineChartAjax from './LineChartAjax';
import {Input} from 'antd';
import FundGroupChart from './FundGroupChart';
import { Row, Col } from 'antd';
import BarChart from './BarChart';
import PieChart from './PieChart';

class PageHome extends Component {
   constructor(){
     super();
     this.state ={
      snos:"000778,600723"
     }
   }  
  
  
  search_click(searchText){
    console.log(searchText);
    this.setState({snos:searchText})
    this.refs.jingzichan.refreshAjaxDataState(searchText);
    this.refs.yingyeshouru.refreshAjaxDataState(searchText);
    this.refs.lirunzonge.refreshAjaxDataState(searchText);
    this.refs.fundgroups.refreshAjaxDataState(searchText.split(',')[0]);
    this.refs.fuzhailv.refreshAjaxDataState(searchText);
    this.refs.jingyingjingliuliang.refreshAjaxDataState(searchText);
    this.refs.fenhongchart.refreshAjaxDataState(searchText);
    this.refs.dagudong.refreshAjaxDataState(searchText);
  }
  
  render() {
    return (
      <div className="App">
      <div><h2>其它功能的Link</h2>
      
      </div>
      <hr></hr>
<p></p>
      <h2>财务基本面数据</h2>
      <div>
        <Input.Search placeholder="input your gainian here" enterButton="Search" size="large" onSearch={this.search_click.bind(this)} style={{width:600}}></Input.Search>
        <br></br>
      </div>
        <div>
          <Row><Col><BarChart ref="fenhongchart" title="每市值对应分红" api ="fenhong" snos={this.state.snos} ></BarChart></Col></Row>
          <Row>
            <Col><PieChart ref="dagudong" title="控股股东情况" api ="dagudong" sno={this.state.snos.split(',')[0]} ></PieChart></Col>
          </Row>
          <Row>
          <Col span={12}><FundGroupChart ref ="fundgroups" sno="000778" title="最近180天来的资金流变化图"></FundGroupChart></Col>          
          </Row>
          <Row>
          <Col span={12}><LineChartAjax ref="yingyeshouru" tablename="lirunbiao" columnname ="TOTALOPERATEREVE"  title="每市值对应的营业收入走势" snos={this.state.snos} api="financepershizhi"/> </Col>
          <Col span={12}><LineChartAjax ref="lirunzonge" tablename="lirunbiao" columnname ="SUMPROFIT"  title="每市值利润总额" snos={this.state.snos} api="financepershizhi"/>    </Col>
          </Row>
          <Row>
          <Col span={12}><LineChartAjax ref="fuzhailv" tablename="zichanfuzhaibiao" columnname ="SUMPROFIT"  title="货币资金+应收票据/短期负债总额" snos={this.state.snos} api="fuzhailv"/></Col>
          <Col span={12}><LineChartAjax ref="jingyingjingliuliang" tablename="xianjinliuliangbiao" columnname ="NETOPERATECASHFLOW"  title="每市值经营活动现金净流量" snos={this.state.snos} api="financepershizhi"/></Col>
          </Row>
          <Row>  
          </Row>
          <Row>                      
          <Col span={12}><LineChartAjax ref="jingzichan" tablename="zichanfuzhaibiao" columnname ="SUMSHEQUITY"  title="每市值对应的净资产走势" snos={this.state.snos} api="financepershizhi"/></Col>
          </Row>
          <Row>                      
          </Row>
        </div>
      </div>
    );
  }
  
}

export default PageHome;
