import React, { Component } from 'react';
import {Input} from 'antd';
import { Row, Col } from 'antd';
import { Chart } from "react-google-charts";

export default class PageGainianPrice extends Component {
    constructor(){
        super();
        this.state ={
            snos:"000778,600723",
            gainianpriceData:{}
           };
        this.getGainianchartdata()
    }

    search_click(searchText){
        console.log(searchText);
        this.state.snos=searchText;
    }



    

    getGainianchartdata(){
        
        this.serverRequest = fetch('https://localhost/api/Price/gainianprice', {
            method: 'GET',
            mode: 'cors',
            //credentials: 'include',
            headers: {
              'Content-Type': 'application/json'
            }           
          }).then(res=>res.json())
          .then((resJson)=>{
            this.setState({
                gainianpriceData:resJson
            })
        })  
    }

    render(){
        return (
        <div className="App">
        <div>
        <Input.Search placeholder="input your gainian here" enterButton="Search" size="large" onSearch={this.search_click.bind(this)} style={{width:600}}></Input.Search>
        <br></br>
         </div>
         <div>
         <Row><Col></Col></Row>

    <div>
    <Chart ref="gainianprice"  width={1800} height={2800} chartType="Line" data ={this.state.gainianpriceData} options={{
      chart: {
        title: '各个概念板块的价格变动',
        subtitle: '各个概念板块相对于大盘的价格变动',
        interpolateNulls: true
      },
    }}></Chart>
    </div>
         </div>         
        </div>
        )
    }
}