import React, { Component } from 'react';
import './App.css';
import 'antd/dist/antd.css';
import { BrowserRouter as Router, Route } from "react-router-dom";
import PageHome from './components/PageHome';
import PageAbout from './components/PageAbout';
import PagePrice from './components/PagePrice';
import PageGainianPrice from './components/PageGainianPrice';
import PageBKSnosPrices from './components/PageBKSnosPrices';

class App extends Component { 
  
   render() {
    return (
      <div className="App">
        <Router>
          <div>            
            <Route exact path="/" component={PageHome} />
            <Route path="/about" component={PageAbout} />     
            <Route path="/price" component={PagePrice} />      
            <Route path="/gainian" component={PageGainianPrice} /> 
            <Route path="/bankuai" component={PageBKSnosPrices} />          
          </div>
        </Router>
      </div>
    );
  }
}

export default App;
