import React, { Component } from 'react';
import {Input} from 'antd';
import { Row, Col } from 'antd';
import { Chart } from "react-google-charts";

/** 
 * 这个是价格的变化数据
*/
export default class PagePrice extends Component {
    constructor(){
        super();
        this.state ={
            snos:"000778,600723",
            d3chgChartData:{},
            hangyepriceData:{}        
           };
        this.getd3chgChartData("000778,600723");   
        this.getHangyechartdata();       
    }

    search_click(searchText){
        console.log(searchText);
        this.state.snos=searchText;
    }

    getd3chgChartData(snostring){
        let postData = snostring.split(',')
        this.serverRequest = fetch('https://localhost/api/Price/d3relchg', {
            method: 'POST',
            mode: 'cors',
            //credentials: 'include',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(postData)
          }).then(res=>res.json())
          .then((resJson)=>{
            this.setState({
                d3chgChartData:resJson
            })
        })  
    }

    getHangyechartdata(){
        
        this.serverRequest = fetch('https://localhost/api/Price/hangyeprice', {
            method: 'GET',
            mode: 'cors',
            //credentials: 'include',
            headers: {
              'Content-Type': 'application/json'
            }           
          }).then(res=>res.json())
          .then((resJson)=>{
            this.setState({
                hangyepriceData:resJson
            })
        })  
    }



    render(){
        return (
        <div className="App">
        <div>
        <Input.Search placeholder="input your gainian here" enterButton="Search" size="large" onSearch={this.search_click.bind(this)} style={{width:600}}></Input.Search>
        <br></br>
         </div>
         <div>
         <Row><Col><Chart ref="d3relchg"  width={880} height={300} chartType="Line" data ={this.state.d3chgChartData} snos={this.state.snos} options={{
      chart: {
        title: '3日累积变化',
        subtitle: '当前价格比过去3日价格的变化',
        interpolateNulls: true
      },
    }}></Chart></Col></Row>

<Chart ref="hangyeprice"  width={1800} height={1800} chartType="Line" data ={this.state.hangyepriceData} options={{
      chart: {
        title: '各个行业的价格变动',
        subtitle: '各个行业相对于大盘的价格变动',
        interpolateNulls: true
      },
    }}></Chart>
    <div>
    </div>
         </div>         
        </div>
        )
    }
}