import * as React from "react";
import { Chart } from "react-google-charts";

export default class FundGroupChart extends React.Component {
  constructor(props) {
    super(props);
    this.state = { lineChartData: {} ,csno:''};
  }

  componentDidMount() {      
       this.refreshAjaxDataState(this.props.sno);       
  }

  refreshAjaxDataState(sno){
    console.log("refreshAjaxDataState start");    

    this.serverRequest = fetch('https://localhost/api/Page/getfundgroups?sno='+sno, {
      method: 'Get',
      mode: 'cors',
      //credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      }      
    }).then(res=>res.json())
    .then((resJson)=>{
      this.setState({
        lineChartData:resJson,
        csno:sno
      })
    }) 
  }

  render() {
    console.log("LineChartAjax.Render start ...")
    //this.getAjaxDataState();
    return (
      <Chart
        width={980}
        height={600}
        chartType="Line"
        loader={<div>Loading Chart...</div>}
        data={this.state.lineChartData}
        options={{
          chart: {
            title: this.props.title,
            subtitle: this.state.csno,
            interpolateNulls: true
          },
        }}
      />);
  }
}