import * as React from "react";
import { Chart } from "react-google-charts";

export default class LineChartAjax extends React.Component {
  constructor(props) {
    super(props);
    this.state = { lineChartData: {} };
  }

  componentDidMount() {      
       this.refreshAjaxDataState(this.props.snos);       
  }

  refreshAjaxDataState(snolist){
    console.log("refreshAjaxDataState start");
    let postData = {
      snos: snolist.split(','), table: this.props.tablename, column: this.props.columnname
    };

    this.serverRequest = fetch('https://localhost/api/Page/'+this.props.api, {
      method: 'POST',
      mode: 'cors',
      //credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(postData)
    }).then(res=>res.json())
    .then((resJson)=>{
      this.setState({
        lineChartData:resJson
      })
    }) 
  }

  render() {
    console.log("LineChartAjax.Render start ...")
    //this.getAjaxDataState();
    return (
      <Chart
        width={880}
        height={300}
        chartType="Line"
        loader={<div>Loading Chart...</div>}
        data={this.state.lineChartData}
        options={{
          chart: {
            title: this.props.title,
            subtitle: this.props.subtitle,
            interpolateNulls: true
          },
        }}
      />);
  }
}